<template>
  <div class="detail">
    <div class="detail__content container">
      <div class="detail__title">
        <h1>{{ dataDetail.title }}</h1>
      </div>
      <div class="detail__project">
        <div class="detail__post">
          {{ dataDetail.description }}
        </div>
        <div class="detail__service">
          <div
            class="detail__service--content"
            v-for="(item, i) in dataDetail.services"
            :key="i"
          >
            <div class="detail__service--name">{{ item.title }}</div>
            <ul>
              <li v-for="(value, z) in item.values" :key="z">{{ value }}</li>
            </ul>
          </div>
        </div>
      </div>
      <div class="detail__list">
        <div
          class="detail__list--item"
          v-for="(box, n) in dataDetail.imageLinks"
          :key="n"
          v-ScrollClass="{ className: 'detail__fadeIn' }"
        >
          <BoxCommon
            :showLink="false"
            :image="box"
            @click="showModal(box, n)"
          ></BoxCommon>
        </div>
      </div>
      <div class="detail__navigation">
        <div
          class="detail__navigation--item"
          @click="handelNavigate(dataPrev?.id, dataPrev?.index)"
        >
          <div v-if="dataPrev">
            <span v-if = !store.state.isScreenMobile>{{ dataPrev?.title }}</span>
            <img v-if = store.state.isScreenMobile src="@/assets/image/icon/icon-prev.png" alt="" />
          </div>
        </div>
        <div
          class="detail__navigation--item"
          @click="handelNavigate(dataNext?.id, dataNext?.index)"
          v-if="dataNext"
        >
          <div v-if="dataNext"> 
            <span v-if = !store.state.isScreenMobile>{{ dataNext?.title }}</span>
            <img v-if = store.state.isScreenMobile src="@/assets/image/icon/icon-next.png" alt="" />
          </div>
        </div>
      </div>
    </div>
    <ModalCommon
      ref="refModal"
      :navigate="true"
      @handeNextClick="handelNext"
      @handePrevClick="handelPrev"
    >
      <div class="detail__modal">
        <div class="detail__modal--wrap">
          <img :src="showImg.link" alt="" />
        </div>
      </div>
    </ModalCommon>
  </div>
</template>
<script setup>
import BoxCommon from "@/components/common/box-common.vue";
import ModalCommon from "@/components/common/modal-common.vue";
import vScrollClass from "@/directive/ScrollClass";
import {
  defineProps,
  onBeforeMount,
  ref,
  reactive,
  watch,
  onMounted,
} from "vue";
import dataLife from "@/data/dataLiffe.Json";
import { useRoute, useRouter } from "vue-router";
import { useStore } from 'vuex';

const store = useStore();

const route = useRoute();
const router = useRouter();

const props = defineProps({
  id: {},
  name: String,
});

const data = ref({});
const dataDetail = ref({});
const refModal = ref(null);
const dataNext = ref(null);
const dataPrev = ref(null);
const showImg = reactive({
  link: "",
  index: "",
});

onBeforeMount(() => {
  window.scrollTo(0, 0);
  if (props.name === "life") {
    data.value = dataLife;
  }

  const indexData = Number(route.query.index);

  dataDetail.value = data.value.find((item) => item.id == props.id);
  if (indexData < data.value.length - 2) {
    dataNext.value = data.value[indexData + 1];
    dataNext.value.index = indexData + 1;
  }
  if (indexData > 0) {
    dataPrev.value = data.value[indexData - 1];
    dataPrev.value.index = indexData - 1;
  }
});

onMounted(() => {
  window.scrollTo(0, 0);
});

const showModal = (img, index) => {
  showImg.link = img;
  showImg.index = index;

  refModal.value.handleShowModal();
};
const handelNext = () => {
  let imgIndex = ++showImg.index;
  const totalImg = dataDetail.value.imageLinks.length - 1;
  if (imgIndex > totalImg) {
    imgIndex = showImg.index = 0;
  }
  showImg.link = dataDetail.value.imageLinks[imgIndex];
};
const handelPrev = () => {
  let imgIndex = --showImg.index;
  const totalImg = dataDetail.value.imageLinks.length - 1;
  if (imgIndex < 0) {
    imgIndex = showImg.index = totalImg;
  }
  showImg.link = dataDetail.value.imageLinks[imgIndex];
};
const handelNavigate = (id, index) => {
  const params = { id: id, name: props.name };
  const query = { index: index, timestamp: Date.now() };
  router.replace({ name: "Detail", params, query });
};
watch(
  () => route.query,
  (newQuery, oldQuery) => {
    if (newQuery.timestamp !== oldQuery.timestamp) {
      router.go(0);
      window.scrollTo(0, 0);
    }
  }
);
// onBeforeRouteUpdate(() => {
//   window.scrollTo(0, 0)
// });
</script>
<style lang="scss" scoped src="@/assets/scss/common/_detail.scss"></style>